import React, {Component} from "react";
import {connect} from "react-redux";
import PropTypes from 'prop-types';
import {OraLabel} from "eshop/components/OraCommonComponents";
import {getSelectedOffer, getSelectedOfferId} from "eshop/modules/configurator/selectors/offers";
import {getIsSalesOfGoodsPage, getSearchValue, getStateAddToCartButton, getCashInvoiceLimit, getCartInvoiceValue} from "eshop/modules/simfree/selectors"
import {
    getClientContext,
    getFiltersUrl,
    getMarketContext,
    getSelectedFilters,
    getSelectedOfferType
} from "eshop/modules/configurator/selectors/filters";
import {addMagnumToCart, addToCart, setErrorCode, setSelectedVariantOnList} from "eshop/modules/simfree/actions/app";
import {selectOffer} from "eshop/modules/configurator/actions/offers";
import DataLayerUtils from "eshop/utils/DataLayerUtils";
import OnlineUtils from "eshop/utils/OnlineUtils";
import OraStockLevelStatusComponent from "eshop/components/OraStockLevelStatusComponent"
import OraPriceSummaryComponent from "eshop/components/OraPriceSummaryComponent"
import OfferType from "../../constants/OfferTypeEnum";
import {
    getAddTerminalToOfferBundleNo,
    getMagnumBundleTemplate,
    getMagnumOfferId,
    getPriceIsNet,
    getProductCodeByBundleNo
} from "eshop/modules/cart/selectors";
import {updateTerminalToOffer} from "eshop/modules/cart/actions/cart";
import DevicePayments from "eshop/modules/simfree/components/list/DevicePayments";
import {constants} from "eshop/modules/configurator/constants";
import ComparatorCheckbox from "eshop/modules/simfree/components/comparator/ComparatorCheckbox";
import {getSelectedCategory} from "../../selectors";
import {getAssistModeStateData} from "../../../checkout/selectors";
import {Sticker} from "../../../configurator/components/lp/offers/Sticker";
import ProcessType from "../../../cart/enum/ProcessType";
import {getMarketContextPrefixUrl} from "eshop/modules/configurator/selectors/metaData"
import OraCashInvoiceLimitComponent from "eshop/modules/simfree/components/modals/OraCashInvoiceLimitComponent";
import {
    isDuet,
    showCanonicalLink
} from "../../selectors";

var carouselId = 0;


class ProductListSingleProductComponent extends React.Component {

    constructor(props) {
        super(props);
        this.id = "react-carousel-" + carouselId++;
        this.page = this.props.marketContextPrefixUrl +(window.location.pathname.includes("/sklep") ? "/sklep" : "/akcesoria");
        this._salesChannelIsPos = this._salesChannelIsPos.bind(this);
        this.state = {isColorChosen: false};
    }

    componentDidMount() {
        this._loadModules();
    }

    componentWillReceiveProps(nextProps) {
        if (!!this.props.addTerminalToOfferBundleNo && nextProps.selectedVariant == null) {
            let variant = this.props.product.variantList.find(v => this.props.productCodeToRemove === v.productId);
            variant && this.props.setSelectedVariantOnList.bind(this, variant, variant.colorDefinition && variant.colorDefinition[0].code, this.props.product.productId)();
        }
    }

    componentWillUpdate() {
        console.log("ProductListSingleProductComponent constr");
        if (this.props.skuSearchValue) {
            console.log("ProductListSingleProductComponent his.props.skuSearchValue");
            let variant = this.props.product.variantList.find(v => this.props.skuSearchValue === v.skuNumber);
            if (variant && (!this.props.selectedVariant || variant.skuNumber !== this.props.selectedVariant.skuNumber)) {
                console.log("ProductListSingleProductComponent his.props.variant");
                this.props.setSelectedVariantOnList(variant, variant.colorDefinition && variant.colorDefinition[0].code, this.props.product.productId);
            }
        }
    }

    componentDidUpdate(prevProps) {
        if (this.props.product.variantList.length !== prevProps.product.variantList.length) {
            $("#carousel_product_" + this.props.product.productId + " :button").remove();
            OPL.UI.fire(OPL.UI.EVENTS.modules.carousel.reinit, "carousel_product_" + this.props.product.productId);
        }
    }

    findProductVariant() {
        for (var i = 0; i < this.props.product.variantList.length; i++) {
            if (this.props.product.variantList[i].productId === this.props.selectedVariant
                || (!this.props.selectedVariant && this.props.product.variantList[i].productId === this.props.product.selectedVariant)) {
                return this.props.product.variantList[i];
            }
        }
        return this.props.product.variantList[0];
    }

    price(){
        function priceFormProduct(product){
            return product.deviceTotalPriceGross || product.priceGross
        }

        let productUnderChange = this.props.productCodeToRemove && this.props.products.filter(
            product => this.props.productCodeToRemove == (product.selectedVariant || product.variantList[0].productId)
        )[0]

        return priceFormProduct(this.props.product) - (productUnderChange ? priceFormProduct(productUnderChange) : 0)
    }

    handleAddToCart(productId) {
        DataLayerUtils.pushSimfreeProductClick(this.findProductVariant(), this.props.product && this.props.product.price,this.props.marketIsB2B ? "B2B" : "B2C", this.props.salesChannel, {loggedEmployee : this.props.assistedModeStateData, assistedEmployee : this.props.assistedModeStateData});
        DataLayerUtils.pushSimfreeAddToCart(this.findProductVariant(), this.props.product && this.props.product.price,this.props.marketIsB2B ? "B2B" : "B2C", this.props.salesChannel, {loggedEmployee : this.props.assistedModeStateData, assistedEmployee : this.props.assistedModeStateData});
        //todo refactor
        this.props.addToCart(productId, "DEFAULT_SALES_OF_GOODS_PROPOSITION$MOB_CPO_SALES_OF_GOODS");
    }

    createProductUrl() {
        if (this.props.showCanonicalLink || !OnlineUtils.isCanonicalLink() || this.state.isColorChosen || !this.props.product.canonicalLink) {
            return this.getUrlParameters() + '&' + this.props.filtersUrl;
        } else {
            return this.page + this.props.product.canonicalLink;
        }
    }

    createInstalmentProductUrl() {
        return this.getUrlParameters() + '&offerType=SIMFREE_INST&processType=INSTALMENT_SALES_OF_GOODS';
  }

    getUrlParameters(){
        let productVariant = this.findProductVariant();
        return this.page + productVariant.productUrl + '?chosenDevice=' + productVariant.productId + '&filterState=' + this.props.filterState + '&selectedCategory=' + this.props.selectedCategory + (this.props.isDuet ? "&isDuet="+this.props.isDuet : "")+"&selectedPropositionId="+this.props.selectedOffer;
    }

    priceWrappedAsProposition() {
        if (this.props.product.inOffer) {
            return {
                deviceData: {
                    inOffer: this.props.product.inOffer
                }
            }
        }
    }

    saveMagnumStore() {
        let magnumStore = {...this.props.magnumStore};
        magnumStore.possibleTransactions.salesChannel = this.props.salesChannel;
        OnlineUtils.saveInSessionStorage(constants.magnumStore, JSON.stringify(magnumStore));
    }

    render() {
        console.log('### this.props.product.productId = ', this.props.product.productId);
        console.log('### this.props.product.code = ', this.props.product.code);
        return (
            <div className="l-col l-col-xsmall-6 l-col-small-4 l-col-medium-3 l-col-4 l-col-xlarge-4  u-no-padding">
                <div id={this.props.product.productId}
                     className="l-row opl-product-box opl-product-box--bordered-small opl-product-box--bordered-medium u-small-no-shadow u-medium-no-shadow u-small-no-margin u-medium-no-margin u-padding-top-l u-padding-l">
                    {this._renderProductImageSection()}
                    {this._renderProductPriceSection()}
                    {this.props.salesChannel != 'WWW' && !this.props.isSalesOfGoodsPage &&
                    <OraPriceSummaryComponent stockLevel={this.findProductVariant().stockLevel}
                                              deviceTotalPrice={this.props.product.deviceTotalPrice}
                                              deviceTotalPriceNet={this.props.product.deviceTotalPriceNet}
                                              deviceTotalPriceGross={this.props.product.deviceTotalPriceGross}
                                              basePrice={this.props.product.inOffer && this.props.product.inOffer.price.base}
                                              proposition={this.props.selectedOfferObject}
                                              shouldShowStock={!this._salesChannelIsPos()}
                                              contractRole={this.props.contractRole}
                                              key={this.props.product.productId}
                                              shouldShowSum={OfferType.CONVERGENT !== this.props.offerType}
                                              offerType={this.props.offerType}
                    />}
                    {this._renderAddToCartSection()}


                </div>
            </div>
        )
    }

    _renderProductImageSection() {
        return (
            <div className="l-col l-col-12  opl-product-box__section">
                <div className="u-animate-height js-layout-fixer-group-1">
                    <div className="js-height-sensitive-element">
                        <div className="l-row u-display_table u-small-block u-medium-block u-no-margin">
                            {this._renderColorComponent()}
                            <div
                                className="l-col l-col-small-12 l-col-medium-9 l-col-9  u-display_table-cell u-small-block u-medium-block u-margin-center">

                                <ProductImage salesChannelIsPos={this._salesChannelIsPos()}
                                              createProductUrl={this.createProductUrl()}
                                              dataLayerOnClick={this._dataLayerOnClick.bind(this)}
                                              findProductVariant={this.findProductVariant()}
                                              sticker={this.props.product.sticker !== null && (this.props.product.sticker.propositionItems &&
                                                        this.props.product.sticker.propositionItems.length === 0 || this.props.product.sticker.propositionItems.includes(this.props.product.bundleTemplateName))
                                                    ? this.props.product.sticker : null}/>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }


    _renderProductPriceTag(product) {

            return (
                <DevicePayments
                    product={product}
                    marketIsB2B={this.props.marketIsB2B}
                    proposition={this.priceWrappedAsProposition()}
                    instalmentTooltipDescription={this.props.instalmentTooltipDescription}
                    productVariant={this.findProductVariant()}
                    instalmentUrl={this.createInstalmentProductUrl()}

                />
            )


    }

    _renderProductPriceSection() {
        return (
            <div
                className="l-col l-col-12  opl-product-box__section u-padding-left-l u-padding-right-l u-small-no-padding u-medium-no-padding">
                <div className="u-animate-height js-layout-fixer-group-2">
                    <div className="js-height-sensitive-element">
                        {(OraStockLevelStatusComponent.isAvailableInStock(this.findProductVariant().stockLevelStatus) && !this._salesChannelIsPos()) &&
                        <OraStockLevelStatusComponent stockLevelStatus={this.findProductVariant().stockLevelStatus}
                                                      stockLevel={this.findProductVariant().stockLevel}
                                                      stockLevelDate={this.findProductVariant().stockLevelDate}
                                                      shouldStockLevelBeVisible={this.findProductVariant().shouldStockLevelBeVisible}/>
                        }
                    </div>
                </div>
                <div className="u-animate-height js-layout-fixer-group-3">
                    <div className="js-height-sensitive-element">
                        <a href={this.createProductUrl()}
                           className="u-padding-top-l u-padding-l u-block u-text-decoration-none"
                           onClick={this._dataLayerOnClick.bind(this)}>
                            {this.findProductVariant().name}
                        </a>
                    </div>
                </div>
                <div className="u-animate-height js-layout-fixer-group-4">
                    <div className="js-height-sensitive-element">
                        {this._renderProductPriceTag(this.props.product)}
                    </div>
                </div>
            </div>
        )
    }

    _renderAddToCartSection() {

        let nonActive = (this.findProductVariant().stockLevel === 0);

        var addToCartClick = () => {
            if (nonActive && !this._salesChannelIsPos()) {
                return
            }

            if(this.props.cartIsFix && this.props.salesChannel === "WWW"){
                this.props.setErrorCode("fixincart");
                return
            }

            if (this.props.selectedFilters.processType === ProcessType.SALE_OF_GOODS) {
                this.handleAddToCart(this.findProductVariant().productId, this.props.selectedOffer);
            } else if ([OfferType.DATA, OfferType.VOICE, OfferType.VOICE_LDF, OfferType.DATA_LDF, OfferType.SIMFREE_INST].indexOf(this.props.offerType) > -1) {
                if (!!this.props.addTerminalToOfferBundleNo) {
                    let productsToRemove = [this.props.productCodeToRemove];
                    let productsToAdd = [this.props.product.selectedVariant || this.props.product.variantList[0].productId];
                    let cartBundle = this.props.addTerminalToOfferBundleNo;
                    let bundle = this.props.selectedOffer;//this.props.product.bundleTemplateName
                    let offer = this.props.selectedOfferObject.rateplanId;
                    this.props.addTerminalToOfferToCart(bundle, offer, cartBundle, productsToRemove, productsToAdd)
                } else {
                    this.props.selectOfferWithDevice(this.findProductVariant().productId, this.props.selectedOffer, this.props.position);
                }
            } else if (OfferType.CONVERGENT === this.props.offerType) {
                this.saveMagnumStore();
                if (!!this.props.addTerminalToOfferBundleNo) {
                    let productsToRemove = [this.props.productCodeToRemove];
                    let productsToAdd = [this.props.product.selectedVariant || this.props.product.variantList[0].productId];
                    let cartBundle = this.props.addTerminalToOfferBundleNo;
                    let bundle = this.props.updateMagnumBundleTemplate;
                    let offer = this.props.updateMagnumOfferId;
                    this.props.addTerminalToOfferToCart(bundle, offer, cartBundle, productsToRemove, productsToAdd);
                } else {
                    this.props.addMagnumToCart(this.findProductVariant().productId);
                }
            }
        };

        var addToCartClickCheckPrice = () => {
            if(this.props.cashInvoiceLimit && this.props.cartInvoiceValue + this.price() > this.props.cashInvoiceLimit){
                  OraCashInvoiceLimitComponent.openPopup({
                      onClickConfirm: addToCartClick
                  })
            } else {
                addToCartClick()
            }
        };

        let isCurrentDevice = !!this.props.addTerminalToOfferBundleNo
            && (this.props.productCodeToRemove == (this.props.product.selectedVariant || this.props.product.variantList[0].productId));

        let isDisabledClass = (nonActive && !this._salesChannelIsPos()) || this.props.isDisableAddToCartButton ? "opl-btn-disabled" : "";

        return (
            <div className="u-animate-height js-layout-fixer-group-5">
                <div className="js-height-sensitive-element">
                    <div>
                        <div
                            className="u-display_table opl-product-box__section u-padding-right-l u-padding-top u-large-hide">
                            <div className="u-display_table-cell">
                                <ComparatorCheckbox product={this.props.product}/>
                            </div>
                        </div>
                        <div
                            className="u-display_table   opl-product-box__section u-padding-left-l u-padding-right-l u-padding-top u-small-hide u-medium-hide">
                            <div className="u-display_table-cell">
                                <ComparatorCheckbox product={this.props.product}/>
                            </div>
                            <div className="u-display_table-cell">
                                <button className={"o-btn opl-btn opl-btn--secondary opl-btn--small " + isDisabledClass}
                                        disabled={!!isCurrentDevice} onClick={addToCartClickCheckPrice}>
                            <span className="opl-ripple-box">
                                <span className="opl-ripple-circle"/>
                            </span>
                                    {isCurrentDevice ? 'Wybrano' : 'Do koszyka'}
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }

    _renderColorComponent() {
        return (
            <div
                className="l-col l-col-medium-3 l-col-3  u-padding-left-s u-padding-right-s u-display_table-cell u-small-hide u-medium-hide">
                <div data-js-module={'common/modules/opl-carousel'}
                     className="opl-wiking-gallery-navigation opl-carousel-vertical-arrows opl-color-chooser u-w-100"
                     id={"carousel_product_" + this.props.product.productId}
                     ref={ref => this.colorCarousel = ref}>
                    <fieldset>
                        <legend className="u-acc-hide">Kolor telefonu</legend>
                        <div className="slick-list">
                            <div className="slick-track">
                                {this._renderColorChooser()}
                            </div>
                        </div>
                    </fieldset>
                </div>
            </div>
        )
    }

    _renderColorChooser() {
        return this.props.product.variantList.map((variant, index) => {
            return (
                <label
                    className="u-w-100 opl-color-chooser__item opl-wiking-gallery-navigation--item u-padding-top u-padding"
                    key={"color_" + index}>
                    <input type="radio" name={"color_" + this.props.product.productId} value={variant.productId}
                           checked={variant.productId == this.findProductVariant().productId}
                           onChange={() => this._onColorChange(variant)}/>
                    <span className="opl-color-chooser__color opl-color-chooser__color--magnum u-margin-center"
                          title={variant.colorDefinition[0].name + "" + (variant.colorDefinition[1] ? ("/" + variant.colorDefinition[1].name) : "")}
                          style={{background: variant && variant.colorDefinition && variant.colorDefinition[0] && variant.colorDefinition[0].cssCode && variant.colorDefinition[0].cssCode.replace(/\ '/g, "")}}>

                             {(variant && variant.colorDefinition && variant.colorDefinition[1] && variant.colorDefinition[1].cssCode) &&
                             <span className="opl-color-chooser__two-color"
                                   style={{background: variant.colorDefinition[1].cssCode.replace(/\ '/g, "")}}>
                                                    </span>}
                </span>
                    <span className="opl-color-chooser__label"/>
                </label>

            )
        }, this);
    }

    _onColorChange(variant){
        this.setState({isColorChosen: true});
        this.props.setSelectedVariantOnList(variant, variant.colorDefinition && variant.colorDefinition[0].code, this.props.product.productId);

    }

    _salesChannelIsPos() {
        return this.props.salesChannel === 'POS' || this.props.salesChannel === 'AKA';
    }

    _dataLayerOnClick() {
        this.saveMagnumStore();
        DataLayerUtils.pushDeviceDetailsEvent(this.findProductVariant(), this.props.product, this.props.selectedOfferObject, this.props.selectedFilters, null, this.props.marketIsB2B ? "B2B" : "B2C", this.props.salesChannel, {loggedEmployee : this.props.assistedModeStateData, assistedEmployee : this.props.assistedModeStateData} );
    }

    _loadModules() {
        OPL.UI.loadModules(this.colorCarousel, [{
            path: 'common/modules/opl-carousel',
            options: {
                autoplay: false,
                arrows: true,
                slide: '.opl-wiking-gallery-navigation--item',
                vertical: true,
                verticalSwiping: true,
                focusOnSelect: true
            }
        }]);

    }
}

const mapStateToProps = () => {
    const productCodeByBundleNo = getProductCodeByBundleNo();
    const magnumBundleTemplate = getMagnumBundleTemplate();
    const magnumOfferId = getMagnumOfferId();

    return (state, ownProps) => ({
        clientContext: getClientContext(state),
        selectedVariant: ownProps.product.selectedVariant,
        offerType: getSelectedOfferType(state),
        selectedOffer: getSelectedOfferId(state),
        selectedOfferObject: getSelectedOffer(state),
        selectedFilters: getSelectedFilters(state),
        isDisableAddToCartButton: getStateAddToCartButton(state),
        filtersUrl: getFiltersUrl(state),
        isSalesOfGoodsPage: getIsSalesOfGoodsPage(state),
        addTerminalToOfferBundleNo: getAddTerminalToOfferBundleNo(state),
        productCodeToRemove: productCodeByBundleNo(state, ownProps.addTerminalToOfferBundleNo),
        marketIsB2B: getMarketContext(state) === "B2B",
        showNet: getPriceIsNet(state),
        skuSearchValue: getSearchValue(state),
        magnumStore: state.magnum,
        updateMagnumBundleTemplate: magnumBundleTemplate(state, ownProps.addTerminalToOfferBundleNo),
        updateMagnumOfferId: magnumOfferId(state, ownProps.addTerminalToOfferBundleNo),
        selectedCategory: getSelectedCategory(state),
        assistModeStateData : getAssistModeStateData(state),
        marketContextPrefixUrl:getMarketContextPrefixUrl(state),
        cashInvoiceLimit: getCashInvoiceLimit(state),
        cartInvoiceValue: getCartInvoiceValue(state),
        isDuet: isDuet(state),
        showCanonicalLink: showCanonicalLink(state)
    });
};


const mapDispatchToProps = (dispatch) => ({
    setSelectedVariantOnList: (selectedVariantCode, colorCode, productCode) => dispatch(setSelectedVariantOnList(selectedVariantCode, colorCode, productCode)),
    addToCart: (productCode, bundleTemplateName) => dispatch(addToCart(productCode, bundleTemplateName)),
    selectOfferWithDevice: (productCode, bundleTemplateName, position) => dispatch(selectOffer(bundleTemplateName, position, productCode)),
    addMagnumToCart: (productCode) => dispatch(addMagnumToCart(productCode)),
    addTerminalToOfferToCart: (bundle, offer, cartBundle, productsToRemove, productsToAdd) => dispatch(updateTerminalToOffer(bundle, offer, cartBundle, productsToRemove, productsToAdd)),
    setErrorCode: (errorCode) => dispatch(setErrorCode(errorCode))
});
ProductListSingleProductComponent.propTypes = {
    salesChannel : PropTypes.string.isRequired
};

const ProductImage = ({salesChannelIsPos, createProductUrl,dataLayerOnClick,findProductVariant, sticker} ) => {
    if (salesChannelIsPos) {
        return <ProductThumbnail href={createProductUrl} onClick={dataLayerOnClick}
                                 sticker={() => <Sticker sticker={sticker} className={"u-position-absolute u-position-top u-overflow-visible"}/>}
                                 product={findProductVariant}/>
    }
    return (findProductVariant.stockLevel === 0) ?
        <ProductThumbnail href={createProductUrl} onClick={dataLayerOnClick}
                          sticker={() => <Sticker sticker={sticker} className={"u-position-absolute u-position-top u-overflow-visible"}/>}
                          product={findProductVariant}
                          unavailable={() => <UnavailableProduct/>}/> :
        <ProductThumbnail href={createProductUrl} onClick={dataLayerOnClick}
                          sticker={() => <Sticker sticker={sticker} className={"u-position-absolute u-position-top u-overflow-visible"}/>}
                          product={findProductVariant}/>;
};
ProductImage.propTypes = {
    salesChannelIsPos : PropTypes.bool,
    createProductUrl : PropTypes.string.isRequired,
    dataLayerOnClick : PropTypes.func.isRequired,
    findProductVariant :PropTypes.string.isRequired,
    sticker : PropTypes.object,
    propositionItems : PropTypes.arrayOf(PropTypes.string),
    bundleTemplateName: PropTypes.string
};

const ProductThumbnail = ({href, onClick, sticker, product, unavailable}) => {
    return (
        <div>
            <div className="opl-product-box__image-wrapper u-text-center u-no-padding">
                <a href={href}
                   className="opl-product-box__image-content "
                   onClick={onClick}>
                    {sticker()}
                    <img src={product.imageUrl}
                         className="cvx opl-product-box__image"
                         alt={"Zobacz szczegóły " + product.name}
                         title={product.name}/>
                </a>
                {unavailable()}
            </div>
        </div>
    )
};
ProductThumbnail.defaultProps = {
    sticker : ()=>{},
    unavailable : ()=>{}
};

const UnavailableProduct = () => {
    return (
                <div className="opl-product-box__unavailable-banner">
                    <p className="u-font-bold u-font-small u-text-center">Produkt chwilowo niedostępny</p>
                </div>
    );
};
export default connect(mapStateToProps, mapDispatchToProps)(ProductListSingleProductComponent)
